.breed-select {
  width: 200px;
  padding: 4px;
  display: none;
}

body {
  letter-spacing: .02em;
  font-family: Roboto, sans-serif;
}

.loader {
  width: 120px;
  height: 120px;
  border: 16px solid #f3f3f3;
  border-top-color: #3498db;
  border-radius: 50%;
  margin: 10% auto auto;
  animation: spin 2s linear infinite;
  display: none;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.error {
  color: red;
  display: none;
}

.cat-info {
  display: none;
}

.item {
  margin: 0;
  padding: 0;
  list-style: none;
}

.item-breed {
  margin-bottom: 10px;
  font-size: 22px;
  font-weight: 500;
  line-height: 1.5;
}

.span {
  font-weight: bold;
}

.img {
  max-width: 30%;
  height: auto;
  margin-top: 10px;
  margin-bottom: 10px;
  display: block;
}

